.lightboxModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.lightboxModal.active {
  opacity: 1;
  visibility: visible;
}

.lightboxContent {
  position: relative;
  max-width: 90%;
  max-height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lightboxImage {
  max-width: 100%;
  max-height: 90vh;
  object-fit: contain;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
}

.lightboxClose {
  position: absolute;
  top: -50px;
  right: 0;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.3);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.lightboxClose:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: rotate(90deg);
}

.lightboxNav {
  position: absolute;
  background: rgba(32, 144, 237, 0.9);
  border: none;
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.lightboxNav:hover {
  background: var(--primary-color);
  transform: scale(1.1);
}

.lightboxNav.prev {
  left: -70px;
}

.lightboxNav.next {
  right: -70px;
}

.lightboxCounter {
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

@media (max-width: 768px) {
  .lightboxNav.prev {
    left: 10px;
  }

  .lightboxNav.next {
    right: 10px;
  }

  .lightboxClose {
    top: 10px;
    right: 10px;
  }

  .lightboxCounter {
    bottom: 10px;
  }
}
